body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Form {
  padding: 0.75rem;
}

@media only screen and (max-width: 767px) {
  #submitButton {
    margin: 0.5em 0;
  }
}

#deleteButton {
  background: transparent;
  float: right;
  position: absolute;
  right: 0.5em;
  top: 0.25em;
  width: 10%;
  max-width: 10%;
}

#eventName {
  text-align: center;
  margin: 0;
}

#numDays {
  text-align: center;
  font-size: 5em;
  max-width: 100%;
  height: auto;
}

#unitTime {
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .ui.stackable.cards > .card {
    padding: 1.5em !important;
  }
}

.App {
  font-size: large;
  padding: 2em;
}

#title {
  font-size: 2em;
}

#subtitle {
  color: #737373;
}

.CountdownCardList {
  padding: 0.5em;
}

